@keyframes moveInBottom {
  0% {
    opacity: 0;
    transform: translateY(-200px);
  }

  80% {
    transform: translateY(10px);
  }

  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}

@keyframes moveInRight {
  0% {
    opacity: 0;
    transform: translateX(-100px);
  }

  80% {
    transform: translateX(10px);
  }

  100% {
    opacity: 1;
    transform: translateX(0px);
  }
}

@keyframes moveInLeft {
  0% {
    opacity: 0;
    transform: translateX(100px);
  }

  80% {
    transform: translateX(-10px);
  }

  100% {
    opacity: 1;
    transform: translateX(0px);
  }
}

@keyframes moveInTop {
  0% {
    opacity: 0;
    transform: translateY(100px);
  }

  80% {
    transform: translateY(-10px);
  }

  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}

.App {
  text-align: center;
}

.Section {
  width: 100%;
  height: 95vh;
}

.Header {
  color: white;
  background: linear-gradient(to right bottom, rgba(160,28,69,.7), rgba(160,28,69,.7)),url("./backgroud_header.jpg");
  background-size: cover;
  background-position: top;
  clip-path: polygon(0 0, 100% 0, 100% 75vh, 0 100%);
}

.Header__container {
  position: relative;
  top: 20vh;
}

.Header__logo {
  width: 225px;
  animation: backwards moveInBottom 1s ease-out;
}

.Header__title {
  font-family: 'Press Start 2P', cursive;
  font-size: 1.3rem;
  animation: backwards moveInRight 1s ease-out .7s;
}

.Header__description {
  margin-top: 15px;
  font-size: 1.1rem;
  font-family: 'Montserrat', sans-serif;
  font-style: italic;
  margin-bottom: 10px;
  animation: backwards moveInLeft 1s ease-out 1.4s;
}

.btn {
  background:  #ffffff;
  color: #A01C45 !important; 
  font-weight: bold;
  padding: 10px 20px;
  border-radius: 100px;
  margin: 20px 10px;
  border: 0;
  outline: 0;
  font-family: 'Montserrat', sans-serif;
  font-size: .9rem;
  transition: all .2s;
}

.btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 20px rgba(0,0,0,.2);
}

.btn:active {
  transform: translateY(-1px);
  box-shadow: 0 5px 10px rgba(0,0,0,.2);
}

.Header__button {
  animation: backwards moveInTop 1s ease-out 2.1s;
}